/* Form */
.form-background {
    background-color: rgba(0, 0, 0, 0.2);
    backdrop-filter: blur(10px);
    width: 100%;
    height: 100%;
    position: fixed;
    top: 0;
    left: 0;
    z-index: 1;
  }
  
  .form-wrapper {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100vh;
  }
  
  .form {
    display: flex;
    position: relative;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 400px;
    height: 400px;
    background-color: #f5f5f5;
    border-radius: 20px;
    padding: 20px;
  }
  
  .form-header {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 50px;
    border-radius: 20px 20px 0 0;
  }
  
  .wrapper {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 100%;
  }
  
  
  .duo-wrapper {
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 100%;
    margin: 10px 0;
  }
  
  input {
    border-radius: 10px;
    padding: 10px;
    margin: 10px 0;
  }
  
  select {
    border-radius: 10px;
    padding: 10px;
    margin: 10px 0;
  }
  
  .close-button {
    position: absolute;
    top: 10px;
    right: 10px;
    font-size: 20px;
    font-weight: 600;
    color: #333;
    background-color: #f5f5f5;
    border-radius: 50vw;
    cursor: pointer;
  }
  